.tab-slider--tab {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  float: left;
  margin: 0;
  padding: 0px;
  list-style: none;
  position: relative;
  border-radius: $tab-border-radius;
  //overflow: hidden;
  background: #e9e9ed;
  height: $tab-height;
  //user-select: none;

  &:after {
    content: '';
    width: 15%;
    background: white;
    display: flex;

    height: 80%;
    position: absolute;
    top: 4px;
    left: 5px;
    transition: all 250ms ease-in-out;
    border-radius: $tab-border-radius;
  }

  &.slideAll:after {
    width: 19%;

    left: 4px;
  }

  &.slideAboutMe:after {
    left: 21%;
    width: 42%;
  }

  &.slideProjects:after {
    left: 62%;
    width: 36%;
  }

  // 	&.slideComponents:after{
  // 	left: 66%;
  // 	width: 34%;
  // }
}

.tab-slider--text {
  font-size: 15px;
  line-height: 1;
  text-align: center;
  padding: 11px $base-margin;
  position: relative;
  z-index: 2;
  cursor: pointer;
  display: inline-block;
  transition: color 250ms ease-in-out;
}

.tab-slider--text:hover {
  opacity: 0.7;
}

.tab-slider--text:active:visited:focus {
  background-color: transparent;
}

.tab-slider--content {
  display: none;

  &.show {
    display: block;
  }
}

